.multiSlider {
  margin-top: 94px;
  .splide__arrow {
    background-color: transparent;

    &--prev {
      left: -2em;
    }

    &--next {
      right: -2em;
    }
  }
}

#endMultiSlider {
  margin-bottom: 150px;

  @media (max-width: 992px) {
    margin-bottom: 50px;
  }
}
